import { nanoid } from "nanoid";
import React from "react";
const defaultValue = {
    isDraggingComponent: false,
    editingComponent: null,
    pageStructure: {
        id: "id",
        pageProps: {},
        nodes: [],
    },
    setDraggingComponent: () => { },
    setEditingComponent: () => { },
    addNodeToPage: () => { },
    addNodeToContainer: () => { },
    updateNode: () => { },
    getNode: () => undefined,
};
export const PageContext = React.createContext(defaultValue);
const PageContextProvider = ({ children }) => {
    const [isDraggingComponent, setDraggingComponent] = React.useState(false);
    const [editingComponent, setEditingComponent] = React.useState(null);
    const [pageStructure, setPageStructure] = React.useState({
        id: nanoid(10),
        pageProps: {},
        nodes: [],
    });
    const addNodeToPage = (index, node) => {
        const newState = {
            ...pageStructure,
            nodes: [
                ...pageStructure.nodes.slice(0, index),
                node,
                ...pageStructure.nodes.slice(index),
            ],
        };
        setPageStructure(newState);
    };
    const addNodeToContainer = (index, node) => {
        const newState = Object.assign({}, pageStructure);
        let parentNode = newState;
        node.parents.forEach((nodeId) => {
            const node = parentNode.nodes.find((node) => (node.id = nodeId));
            if (node) {
                parentNode = node;
            }
        });
        const newNodes = [
            ...parentNode.nodes.slice(0, index),
            node,
            ...parentNode.nodes.slice(index),
        ];
        parentNode.nodes = newNodes;
        setPageStructure(newState);
    };
    const updateNode = (updatedNode) => {
        const newState = Object.assign({}, pageStructure);
        let parentNode = newState;
        updatedNode.parents.forEach((nodeId) => {
            const node = parentNode.nodes.find((node) => (node.id = nodeId));
            if (node) {
                parentNode = node;
            }
        });
        const indexToUpdate = parentNode.nodes.findIndex((n) => (n.id = updatedNode.id));
        if (indexToUpdate !== -1) {
            const newNodes = [
                ...parentNode.nodes.slice(0, indexToUpdate),
                updatedNode,
                ...parentNode.nodes.slice(indexToUpdate + 1),
            ];
            parentNode.nodes = newNodes;
        }
        setPageStructure(newState);
    };
    const getNode = (nodeId, parentNodes) => {
        let parent = pageStructure;
        parentNodes.forEach((parentId) => {
            const newParent = parent.nodes.find((x) => x.id === parentId);
            if (newParent)
                parent = newParent;
        });
        return parent.nodes.find((x) => x.id === nodeId);
    };
    const value = {
        isDraggingComponent,
        setDraggingComponent,
        editingComponent,
        setEditingComponent,
        pageStructure,
        addNodeToPage,
        addNodeToContainer,
        updateNode,
        getNode,
    };
    return React.createElement(PageContext.Provider, { value: value }, children);
};
export default PageContextProvider;
